import React from 'react';
import { Route, NavLink, Link } from 'react-router-dom';
import connect from '@redux/connect';
import Nav from '@js/nav';
// ===================================================================== 
import Router from './router';
import logo from '@images/logo.png';
// =====================================================================
class ReactIndex extends React.Component {
	componentDidMount(){
		Nav()
	}
	render(){
		return (
			<div className="full-screen">
				<header className="header">
					<h1 className="logo"><Link to="/draft"><img src={logo} alt=""/></Link></h1>
					<i className="font-navicon small-nav" id="smallNav"></i>
				</header>
				<section className="container">
					<aside className="navigation oys" id="navigation">
						<ul>
							<li>
								<h3 className="js-tit"><span></span><i className="font-angle-right"></i></h3>
								<menu>
									<li><NavLink to="/draft/base-editor" activeClassName="active"></NavLink></li>
								</menu>
							</li>
							<li>
								<h3 className="js-tit"><span>RichUtils </span><i className="font-angle-right"></i></h3>
								<menu>
									<li><NavLink to="/draft/RichUtils/toggleInlineStyle" activeClassName="active">toggleInlineStyle </NavLink></li>
									<li><NavLink to="/draft/RichUtils/toggleBlockType" activeClassName="active">toggleBlockType </NavLink></li>
									<li><NavLink to="/draft/RichUtils/toggleLink" activeClassName="active">toggleLink </NavLink></li>
									<li><NavLink to="/draft/RichUtils/blockRender" activeClassName="active">blockRender </NavLink></li>
								</menu>
							</li>
						</ul>
					</aside>
					<main className="oxys">
						<section className="content">
							{/* ng router-outletvue router-view */}
							<Route component={Router} /> 
						</section>
					</main>
				</section>
			</div>
		)
	}
}

export default connect(ReactIndex)



